<template>
<header class="main-header">
    <div class="logo">
        <img src="https://logo.com/image-cdn/images/kts928pd/production/4eb435769b8d8d259cd5d6cd59ffb38652907841-700x394.png?w=1080&q=72" alt="">
    </div>
    <nav>
       <input type="text" v-model="search" placeholder="搜尋商品"><span><img src="@/assets/image/icon/search.png" alt="搜尋" style="vertical-align: bottom"></span>
       <RouterLink to="/about"><a class="navItem" style="margin-left: 150px">關於我們</a></RouterLink>
       <RouterLink to="/news"><a class="navItem">最新消息</a></RouterLink>
       <RouterLink to="/Products"><a class="navItem">產品介紹</a></RouterLink>
       <RouterLink to="/contact"><a class="navItem">聯絡我們</a></RouterLink>
       <RouterLink to="/login"><a class="navItem"><i class="fa-solid fa-cart-shopping" style="color: #9f9f9f;"></i></a></RouterLink>
       <RouterLink to="/shoppingcart" class="navItem"><img src="@/assets/image/icon/shopping_cart.png" alt="購物車" style="vertical-align: bottom"></RouterLink>
    </nav>
</header>
<button class="filter" @click="toggleOrder" :class="{ 'active' : isOrderToggled }">價格(由低至高)</button>
<div class="wrap">
  <div class="product" v-for="product in matchingResults" :key="product.id">
    <img :src="product.src" :alt="product.productName">
    <div class="info">
        <p>{{ product.productName }}</p>
        <p>NT${{ product.price }}</p>
    </div>
    <button class="addBtn" @click= addtoCart()>加入購物車</button>
</div>
</div>

</template>

<script>
import { ref, computed } from 'vue'
import { useStore } from 'vuex'
import Swal from 'sweetalert2'

export default {

    name: 'Products',

    setup(){

        //商品資訊
        const products = ref([
            {
                id: 1,
                productName: "陽光沙灘肯尼",
                src: require("@/assets/image/陽光沙灘肯尼.jpg"),
                price: 1080
            },
            {
                id: 4,
                productName: "足球選手肯尼",
                src: require('../assets/image/足球選手肯尼.jpg'),
                price: 1010
            },
            {
                id: 5,
                productName: "芭蕾舞孃芭比",
                src: require('../assets/image/芭蕾舞孃芭比.jpg'),
                price: 1000
            },
            {
                id: 6,
                productName: "Y2K繽紛芭比",
                src: require('../assets/image/Y2K繽紛芭比.webp'),
                price: 1300
            },
            {
                id: 7,
                productName: "奢華度假芭比",
                src: require('../assets/image/奢華度假芭比.jpg'),
                price: 1100
            },
            {
                id: 9,
                productName: "粉色洋裝芭比",
                src: require('../assets/image/粉色洋裝芭比.webp'),
                price: 1500
            },
        ])    

        
        const search = ref('')
        const isOrderToggled = ref(false);
        const toggleOrder = () => {
           isOrderToggled.value = !isOrderToggled.value;
        }

        const matchingResults = computed(() => {
            //搜尋
            let results = products.value.filter((product) => product.productName.includes(search.value));
           
            //價格排序
            if(isOrderToggled.value){
               results.sort((a, b) => {
                return a.price > b.price ? 1 : -1
             })
            }
            return results
        })

        function addtoCart(){
            Swal.fire({
                text: "商品已加入購物車！"
            });
        }
        //加入購物車
        /*const addtoCart = (name1, price1, img1 ) => {
    
            console.log([name1, price1, img1]);
                
            for (let i = 0; i < this.$store.state.shoppingCart.length; i++) {
                if (img1 === this.$store.state.shoppingCart[i].src) {
                this.$store.state.shoppingCart[i].count += 1;
                return;
                }
            }

            this.$store.state.shoppingCart.push({
                src: img1,
                productName: name1,
                price: price1,
            });

            console.log(this.$store.state.shoppingCart);
        }
        */
       
    return{ products, search, matchingResults, toggleOrder, isOrderToggled, addtoCart }
  }
}
</script>

<style>
    *,*::before,*::after{
        box-sizing: border-box;
    }
    img{
        max-width: 100%;
        height: auto;
    }
    .wrap{
        display: flex;
        flex-wrap: wrap;
        max-width: 1000px;
        width: 100%;
        margin: 0 auto;
        padding: 0 100px;
    }
    .main-header{
        background-color: pink;
        padding: 15px 45px 15px 70px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .logo{
        width: 120px;
    }
    input{
        font-size: 14px;
        margin-right: 3px;
        border-radius: 50px;
        border: 1px #ccc solid;
        padding: 5px 0px 3px 10px;
    }
    input:focus{
        outline: none;
    }
    span{
        display: inline-block;
        margin-right: 30px;
    }
    a.navItem{
        padding-right: 10px;
    }
    .filter{
        font-size: 16px;
        color: #000;
        background-color: #fff;
        border: 1px solid #e33fa0;
        padding: 5px 10px;
        border-radius: 20px;
        margin-top: 20px;
        margin-left: 250px;
        cursor: pointer;
    }
    .active{
        background-color: #ffdce9;
    }
    .product{
        width: 30%;
        display: flex;
        flex-wrap: wrap;
        margin: 10px 10px;
    }
    .product .info{
        display: flex;
        margin: 10px auto;
    }
    .product p:first-child{
        margin-right: 10px;
    }
    .addBtn{
        border-radius: 5px;
        border: none;
        background-color: #e33fa0;
        color: #fff;
        font-size: 15px;
        width: 100px;
        height: 30px;
        cursor: pointer;
        margin: 10px auto;
    }
</style>